<template>
    <div>  
        <div class="loginNav">
            <img src="../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">班级简介</p>
        </div>
        <div class="content f28 c333" style="padding:0.84rem 0 1.7rem;">
            <div class="pl-30" style="border-bottom:.06rem solid #f1f1f1" @click="$router.push({path:'/classIntro'})">
                <span>班级简介</span>
                <img :src="more">
            </div>
            <ul class="pl-30">
                <li class="b_line" @click="$router.push({path:'/dutyList'})">
                    <span>值日生设置</span>
                    <img :src="more">
                </li>
                <li class="b_line" @click="$router.push({path:'/faceEntry'})">
                    <span>学生人脸录入设置</span>
                    <img :src="more">
                </li>
                <li class="b_line" @click="$router.push({path:'/cadresList'})">
                    <span>班干部设置</span>
                    <img :src="more">
                </li>
                <li class="b_line" @click="$router.push({path:'/honorList',query:{type:1}})">
                    <span>班级荣誉</span>
                    <img :src="more">
                </li>
                <li class="b_line" @click="$router.push({path:'/honorList',query:{type:2}})">
                    <span>班级风采设置</span>
                    <img :src="more"> 
                </li>
                <li class="b_line" @click="$router.push({path:'/classCard'})">
                    <span>班牌首页轮播图设置</span>
                    <img :src="more">
                </li>
                <li class="b_line" @click="$router.push({path:'/rest'})">
                    <span>班级作息设置</span>
                    <img :src="more">
                </li>
            </ul>
        </div>
    </div>
</template>

<script>

export default {
  name: 'HelloWorld',
    data () {
        return {
            msg: 'Welcome to Your Vue.js App',
            more:require('../../assets/my/more.png'),
        }
    },
    methods : {
        returns(){
            this.$router.push({path:'/indexs'});//返回上一层
        },
        issue(){

        },
        isChecked(index){
            var list=this.list;
            list[index].checked=!list[index].checked;

            var btn=true;
            for(var i in list){
                if(!list[i].checked){
                    btn=false;
                }
            }
            this.checkedAll=btn;
            this.list=list;
            
        },
        selectAll(){
            this.checkedAll=!this.checkedAll;
            var list=this.list;
            if(this.checkedAll){
                for(var i in list){
                    list[i].checked=true;
                }
            }else{
                for(var i in list){
                    list[i].checked=false;
                }
            }
            this.list=list;
            
        }
    }
}
</script>

<style scoped>
     /* 修改组件样式 */
     .loginNav{
        position: fixed;
    }
    .content{
        line-height: 1rem;
    }
    .content img{
        width:.3rem;
        height:.3rem;
        margin-right: .2rem;
    }
    .content div{
        display: flex;
        justify-content: space-between;
        align-items: center; 
    }
    li{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
</style>




